// jestSetup.js

import 'react-native-gesture-handler/jestSetup'; // setup officiel
import { jest } from '@jest/globals';

// Ignore les warnings liés à Animated(View)
const originalError = console.error;
console.error = (...args) => {
  if (
    typeof args[0] === 'string' &&
    args[0].includes('An update to Animated(View) inside a test was not wrapped in act')
  ) {
    return;
  }
  originalError(...args);
};

// Mock NativeAnimatedHelper pour éviter les warnings
jest.mock('react-native/Libraries/Animated/NativeAnimatedHelper');

// Mock react-navigation
jest.mock('@react-navigation/native', () => {
  return {
    ...jest.requireActual('@react-navigation/native'),
    useNavigation: () => ({
      navigate: jest.fn(),
      goBack: jest.fn(),
    }),
  };
});

// Mock ScrollView de react-native-gesture-handler pour Jest
jest.mock('react-native-gesture-handler', () => {
  const ActualGestureHandler = jest.requireActual('react-native-gesture-handler');
  return {
    ...ActualGestureHandler,
    ScrollView: jest.fn().mockImplementation(({ children }) => children),
    TouchableOpacity: jest.fn().mockImplementation(({ children, onPress }) => {
      return <div onClick={onPress}>{children}</div>;
    }),
  };
});
